.statistics {
  background-color: rgb(240, 240, 240);
  border-radius: 5px;
  display: block;
  margin: 20px auto;
  width: 450px;
  transition: transform 0.2s;
}
.statistics:hover {
  box-shadow: 10px 5px 5px rgba(0, 0, 0, 0.4);
  transform: scale(1.1);
}
.statistics .title {
  color: grey;
  text-transform: capitalize;
  text-align: center;
  padding-top: 10px;
}
.statistics .statList {
  display: flex;
  list-style: none;
  justify-content: space-around;
  align-items: stretch;
  margin: 20px 0px;
  padding-bottom: 10px;
}
.statistics .statList .item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 75px;
  font-weight: 600;
  border-radius: 5px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.4);
  transition: transform 0.2s;
}
.statistics .statList .item:hover {
  transform: scale(1.1);
}
.statistics .statList .item .label {
  color: whitesmoke;
  text-shadow: 0 0 3px #000, 0 0 5px #fff;
  margin: 10px auto;
  font-size: 14px;
}
.statistics .statList .item .percentage {
  color: #000;
  text-shadow: 0 0 3px #fff, 0 0 5px #000;
  margin-bottom: 10px;
}/*# sourceMappingURL=Statistics.module.css.map */